import React from 'react';
import Todo from "./Todo";

const TodoList = ({ todos }) => {
    console.log(todos);
    return (
        <div className="todo-container">
            <ul className="todo-List">
                {todos.map(a=>(
                    <Todo text={a.text}/>
                ))}
            </ul>
        </div>
    );
};

export default TodoList;